import React, { useState, useEffect } from 'react';
import { Draggable, Droppable } from '@hello-pangea/dnd';
import { sendApiRequest } from 'lib/api/public';

export const dropStyle = (snapshot) => {
    return { backgroundColor: snapshot.isDraggingOver ? '#ffffff' : 'inherit' }
}

export const renderDroppable = (droppableId: string, renderContent: (snapshot) => React.ReactNode) => {
    return (
        <Droppable droppableId={droppableId}>
            {(provided, snapshot) => (
                <div
                    ref={provided.innerRef}
                    {...provided.droppableProps}
                >
                    {renderContent(snapshot)}
                    {provided.placeholder}
                </div>
            )}
        </Droppable>
    )
}


export const addTagToEditor = (result, quill) => {
    const dest = result.destination;
    const { draggableId } = result;

    const newText = draggableId;


    if (dest) {
        const { droppableId } = dest;

        if (droppableId === 'droppable-editor' && quill && quill.current) {
            const editor = quill.current.getEditor();
            const selection = editor.getSelection();

            let cursorPosition = editor.getLength();

            if (selection) {
                cursorPosition = editor.getSelection().index;
            }

            editor.insertText(cursorPosition, newText);
        }
    }
}

const Tags = () => {
    const [tags, setTags] = useState([]);

    useEffect(() => {
        sendApiRequest({
            path: '/candidates/statuses/tags',
            method: 'GET'
        }).then((response) => {
            setTags(response.tags);
        });
    }, []);

    const renderTags = () => {
        return (
            <>
                {tags.map((tag, index) => (
                    <Draggable draggableId={tag.value} index={index}>
                        {(provided) => (
                            <div
                                ref={provided.innerRef}
                                {...provided.draggableProps}
                                {...provided.dragHandleProps}
                            >
                                <div style={{ backgroundColor: '#f8f8f8', padding: '5px', borderBottom: '1px solid #e0e0e0', cursor: 'grab' }}>
                                    {tag.text}
                                </div>
                            </div>
                        )}
                    </Draggable>
                ))}
            </>
        );
    }

    return (
        <>
            {renderDroppable('initial', () => {
                return (
                    <>
                        {renderTags()}
                    </>
                )
            })}
        </>
    )
};

export default Tags;
