import React, { useRef, useState } from 'react';
import {
    Box,
    Button,
    Dialog,
    DialogContent,
    DialogTitle,
    IconButton,
    TextField,
} from '@mui/material';
import { DragDropContext } from '@hello-pangea/dnd';
import CloseIcon from '@mui/icons-material/Close';
import notify from 'lib/notify';
import { sendApiRequest } from 'lib/api/public';
import { renderField } from '../../components/utils';
import Editor from '../../components/common/Editor';
import Tags, { renderDroppable, dropStyle, addTagToEditor } from 'components/common/Tags';

type Trigger = {
    _id: string;
    action: string;
    description: string;
    bcc: string;
    subject: string;
    body: string;
}

type Props = {
    onClose: () => void;
    onSuccess: () => void;
    statusId: string;
    trigger?: Trigger;
};

const TriggerForm = ({ onClose, onSuccess, statusId, trigger }: Props) => {
    const [description, setDescription] = useState(trigger?.description || '');
    const [bcc, setBcc] = useState(trigger?.bcc || '');
    const [subject, setSubject] = useState(trigger?.subject || '');
    const [body, setBody] = useState(trigger?.body || '');
    const [saving, setSaving] = useState(false);

    const bbcInputRef = useRef(null);
    const subjectInputRef = useRef(null);

    let quill = useRef(null);

    const onSave = async () => {
        if (!bcc || !subject) {
            notify('Please fill the required fields');
            return;
        }

        setSaving(true);

        try {
            await sendApiRequest({
                path: '/candidates/statuses/manage-triggers',
                method: 'POST',
                data: {
                    _id: trigger?._id,
                    statusId,
                    description,
                    bcc,
                    subject,
                    body
                }
            });

            onSuccess();

            setSaving(false);
        } catch (error) {
            notify(error);
        }

    };

    const renderSingleField = (value: string, label: string, onChange, style={}, required=false) => {
        let ref;

        if (label === 'To (BCC)') {
            ref = bbcInputRef;
        }

        if (label === 'Subject') {
            ref = subjectInputRef;
        }

        return (
            <>
                {renderField({ label, required, renderControl: () => {
                    return (
                        <TextField
                            fullWidth
                            value={value}
                            onChange={(e) => onChange(e.target.value)}
                            style={style}
                            inputRef={ref}
                            inputProps={{ autoFocus: true }}
                        />
                    )
                }})}
            </>
        )
    }

    const onDragEnd = (result) => {
        const dest = result.destination;
        const { draggableId } = result;

        const newText = draggableId;

        if (dest) {
            const { droppableId } = dest;

            addTagToEditor(result, quill);

            if (droppableId === 'droppable-bcc') {
                setBcc(`${bcc}${newText}`);

                if (bbcInputRef && bbcInputRef.current) {
                    bbcInputRef.current.click();
                }
            }

            if (droppableId === 'droppable-subject') {
                setSubject(`${subject}${newText}`);

                if (subjectInputRef && subjectInputRef.current) {
                    subjectInputRef.current.focus();
                }
            }
        }
    }

    return (
        <Dialog onClose={onClose} aria-labelledby="modal-title" fullScreen open sx={{ width: '60%', left: 'inherit' }}>
            <DialogTitle id="modal-title">
                <Box display="flex" alignItems="center">
                    <Box flexGrow={1} >Add trigger</Box>
                    <Box>
                        <IconButton onClick={onClose}>
                            <CloseIcon />
                        </IconButton>

                        <Button variant="contained" color="success" onClick={onSave} disabled={saving}>
                            Save
                        </Button>
                    </Box>
                </Box>
            </DialogTitle>

            <DialogContent sx={{ backgroundColor: '#f8f8f8' }}>
                <br />

                <DragDropContext onDragEnd={onDragEnd}>
                    {renderDroppable('initial', () => {
                        return (
                            <div style={{ width: '38%', float: 'right', marginTop: '-20px', fontSize: '12px' }}>
                                <h3>Tags</h3>

                                <div style={{ height: '800px', overflow: 'scroll' }}>
                                    <Tags />
                                </div>
                            </div>
                        )
                    })}

                    <div style={{ width: '60%', float: 'left' }}>
                        {renderSingleField(description, 'Description', setDescription)}

                        <hr /> <br />

                        {renderDroppable('droppable-bcc', (snapshot) => {
                            return renderSingleField(bcc, 'To (BCC)', setBcc, dropStyle(snapshot), true)
                        })}

                        {renderDroppable('droppable-subject', (snapshot) => {
                            return renderSingleField(subject, 'Subject', setSubject, dropStyle(snapshot), true)
                        })}

                        {renderDroppable('droppable-editor', (snapshot) => {
                            return (
                                <>
                                    {renderField({ label: 'Body', renderControl: () => {
                                        return (
                                            <Editor
                                                style={{ height: '300px', ...dropStyle(snapshot) }}
                                                value={body}
                                                onChange={(value) => setBody(value)}
                                                renderExtraContent={(quillRef) => {
                                                    quill = quillRef;
                                                    return null;
                                                }}
                                            />
                                        )
                                    }})}
                                </>
                            )
                        })}
                    </div>
                </DragDropContext>

            </DialogContent>
        </Dialog>
    );
};

export default TriggerForm;
